}

@-webkit-keyframes boxShadow {
  0% {
    box-shadow: none;
  }
  100% {
    box-shadow: inset 0 -13px 0 #fff, inset 0 -23px 0 #ff641c;
  }
}

@keyframes boxShadow {
  0% {
    box-shadow: none;
  }
  100% {
    box-shadow: inset 0 -13px 0 #fff, inset 0 -23px 0 #ff641c;
  }
}

.projects {
